/* === Catnap Theme, applies when <body> has .theme-cat ==================== */
/* friendly rounded font */
@import url("https://fonts.googleapis.com/css2?family=Nunito:wght@600;800&display=swap");

:root {
  --cat-cream: #fff7ec;
  --cat-peach: #ffd9c2;
  --cat-caramel: #f4b183;
  --cat-cocoa: #5b4636;
  --cat-ink: #27303a;
  --cat-mint: #c9f1e5;
  --cat-salmon: #ff9aa2;
  --cat-yellow: #ffe59a;
  --cat-red: #ffb3b3;
  --cat-blue: #cfe9ff;
}

/* soft paw print background */
body.theme-cat {
  color: var(--cat-ink);
  font-family: "Nunito", system-ui, -apple-system, Segoe UI, Roboto, Arial, sans-serif;
  background:
    radial-gradient(1200px 600px at -20% -10%, rgba(255,217,194,.35), transparent 60%),
    radial-gradient(900px 500px at 110% 10%, rgba(201,241,229,.35), transparent 60%),
    url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' width='56' height='56' viewBox='0 0 56 56'><g fill='%23f4b183' opacity='0.18'><circle cx='12' cy='12' r='6'/><circle cx='40' cy='10' r='5'/><circle cx='18' cy='40' r='5'/><path d='M26 24c6 0 12 4 12 8s-6 6-12 6-12-2-12-6 6-8 12-8z'/></g></svg>") repeat;
  background-size: cover, cover, 56px 56px;
}

/* cards and editor */
body.theme-cat .editor-content,
body.theme-cat .help-card,
body.theme-cat .panel,
body.theme-cat .box {
  background: var(--cat-cream);
  border: 1px solid #eddcc8;
  border-radius: 14px;
  box-shadow: 0 10px 24px rgba(91,70,54,.08);
}

/* title with tiny cat ears */
body.theme-cat .app-title {
  color: var(--cat-cocoa);
  font-weight: 800;
  letter-spacing: .2px;
  position: relative;
  display: inline-block;
  padding-top: 6px;
}
body.theme-cat .app-title::before,
body.theme-cat .app-title::after {
  content: "";
  position: absolute;
  top: -8px;
  width: 14px;
  height: 14px;
  background: var(--cat-peach);
  border: 2px solid var(--cat-cocoa);
  border-bottom: none;
  transform: rotate(45deg);
  border-radius: 2px 2px 0 0;
}
body.theme-cat .app-title::before { left: -18px; }
body.theme-cat .app-title::after  { right: -18px; }

/* version badge */
body.theme-cat .version-badge {
  display: inline-block;
  margin-top: 6px;
  padding: 4px 10px;
  font-weight: 700;
  color: var(--cat-cocoa);
  background: var(--cat-peach);
  border: 1px solid #f2c3a1;
  border-radius: 999px;
}

/* controls */
body.theme-cat select,
body.theme-cat .btn,
body.theme-cat button,
body.theme-cat .toggle {
  color: var(--cat-ink);
  background: #ffffff;
  border: 2px solid var(--cat-caramel);
  border-radius: 12px;
  padding: 8px 12px;
  transition: transform .05s ease, box-shadow .12s ease;
  box-shadow: 0 2px 0 rgba(91,70,54,.15);
}
body.theme-cat button:hover,
body.theme-cat .btn:hover,
body.theme-cat select:hover {
  transform: translateY(-1px);
  box-shadow: 0 6px 14px rgba(91,70,54,.12);
}

/* stats pills */
body.theme-cat .bg-gray-100,
body.theme-cat .bg-green-100,
body.theme-cat .bg-blue-100 {
  background: var(--cat-cream) !important;
  border: 1px solid #eddcc8;
  color: var(--cat-ink);
  border-radius: 999px !important;
}

/* legend squares */
body.theme-cat .legend-yellow,
body.theme-cat .legend-red,
body.theme-cat .legend-blue {
  width: 14px; height: 14px; border-radius: 4px; display: inline-block;
  border: 1px solid #e6d5c1;
}
body.theme-cat .legend-yellow { background: var(--cat-yellow); }
body.theme-cat .legend-red    { background: var(--cat-red); }
body.theme-cat .legend-blue   { background: var(--cat-blue); }

/* hard word legend sample */
body.theme-cat .legend-underline-purple {
  text-decoration: underline dotted #c47dff;
  text-underline-offset: 3px;
  font-weight: 800;
}

/* tooltips */
body.theme-cat #ss-tooltip {
  background: #fffdf9 !important;
  color: var(--cat-ink) !important;
  border: 1px solid #eddcc8 !important;
  border-radius: 10px !important;
  box-shadow: 0 10px 20px rgba(91,70,54,.12) !important;
}

/* sentence highlights, readable text color */
body.theme-cat mark[data-highlight-color="yellow"] {
  background-color: rgba(255,229,154,.6);
  color: #2a2a2a;
  border: 1px solid #f4d67e;
  border-radius: 6px;
  padding: 0 .08em;
}
body.theme-cat mark[data-highlight-color="red"] {
  background-color: rgba(255,179,179,.6);
  color: #2a2a2a;
  border: 1px solid #f3a3a3;
  border-radius: 6px;
  padding: 0 .08em;
}
body.theme-cat mark[data-highlight-color="blue"] {
  background-color: rgba(207,233,255,.55);
  color: #2a2a2a;
  border: 1px solid #b8daf7;
  border-radius: 6px;
  padding: 0 .08em;
}

/* purple underline for hard words, shows above highlights */
body.theme-cat .editor-content span[data-hard-word="1"] {
  text-decoration: none;
  border-bottom: 2px dotted #c47dff;
  padding-bottom: 1px;
}

/* help accordion pretties */
body.theme-cat .help-summary {
  background: #fff1e7;
  color: var(--cat-cocoa);
  font-weight: 800;
  border-bottom: 1px solid #f5e2d0;
}
body.theme-cat .help-pre {
  background: #fffdf9;
  color: #1f2937;
  border-color: #eddcc8;
  border-radius: 10px;
}

/* focus ring */
body.theme-cat *:focus-visible {
  outline: 2px solid var(--cat-caramel);
  outline-offset: 2px;
}
